.index {
  height: 100vh;
  @include media("<tablet") {
    height: calc((var(--vh, 1vh) * 100) - 54px);
    overflow: hidden; }

  &__static {
    @include container--content();
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
    height: 100%;
    padding-top: 172px;
    padding-bottom: 52px;
    z-index: 2;
    @include media("<huge") {
      padding-top: 172px;
      padding-bottom: 52px; }
    @include media("<tablet") {
      padding-top: 65px;
      height: calc((var(--vh, 1vh) * 100) - 54px); }
    @media screen and (max-height: 750px) and (min-width: 1440px) {
      padding-top: 140px; } }

  &__hash-title {
    @include subtitle(7.2rem);
    @include gbackground(100%, 0%);
    @include resetmargin();
    display: inline-block;
    margin-bottom: 27px;
    padding: 0 17px;
    font-weight: 900;
    color: $white;
    @include media("<desktop") {
      font-size: 5rem; }
    @include media("<tablet") {
      font-size: 3rem; }
    @media screen and (max-height: 750px) {
      margin-bottom: 5px; } }

  &__main-title {
    @include subtitle(1.8rem);
    line-height: 3.0rem;
    text-transform: uppercase;
    font-weight: 900;
    max-width: 365px;
    @include media("<desktop") {
      font-size: 1.6rem; }
    @include media("<tablet") {
      max-width: 70%;
      font-size: 1.2rem;
      line-height: 2rem; }
    @media screen and (max-height: 750px) and (min-width: 1440px) {
      margin-bottom: 5px; } }

  &__video-block {
    position: relative;
    justify-content: center;
    margin: auto 0 20px;
    padding: 15px 0 0;
    @include media("<huge") {
      padding-top: 0;
      margin-top: 10px; }
    @media screen and (max-height: 750px) and (min-width: 1440px) {
      margin-top: 5px; } }

  &__video-mask {
    opacity: 0;
    z-index: -100;
    position: absolute; }

  &__video-wrap {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 340px;
    width: 350px;
    background-image: url("../img/index-vid-bg.jpg");
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: 120%;
    clip-path: url(#my-clip);
    img {
      width: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1; }
    @include media("<huge") {
      height: 180px;
      width: 200px; }
    @media screen and (max-height: 800px) and (min-width: 1440px) {
      height: 240px;
      width: 260px; }
    @include media("<tablet") {
      display: none; } }




  &__video-play-btn {
    @include subtitle(2.8rem);
    font-weight: 900;
    z-index: 2;
    fill: $white;
    cursor: pointer;
    //transition: transform $transition
    //transform: scale(1)
    color: $white;
    text-shadow: 0 0 3px $black;
    text-align: center;
    //&:hover
 }    //transform: scale(1.05)

  &__video-mobile-btn {
    display: none;
    @include media("<tablet") {
      position: fixed;
      bottom: 0;
      left: 0;
      appearance: none;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 46px;
      width: 100%;
      z-index: 10;
      @include subtitle(1.2rem);
      letter-spacing: 2px;
      @include gbackground (100%);
      color: $white;
      padding: 3px 7px;
      text-transform: uppercase;
      font-weight: 600; } }





